:root {
  --color-black: #000;
  --color-white: #fff;
  --color-light-gray: #F1EEE5;
  --color-dark: #191919;
  --color-gray: #979797;
}

@font-face {
  font-family: "Ronnia Bold";
  src: url("../fonts/Ronnia Bold.woff2") format("woff2"), url("../Ronnia Bold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "MyriadPro";
  src: url("../fonts/MyriadPro.woff2") format("woff2"), url("../MyriadPro.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

