* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
}

body {
  font-family: "MyriadPro", sans-serif;
  font-size: 12px;
  background-color: var(--color-white);
}

a,
a:hover {
  text-decoration: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
label {
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  border: none;
  cursor: pointer;
  padding: 0;
  background: transparent;
  white-space: nowrap;
}

textarea,
button,
input {
  outline: none !important;
  border: none;
}

.site-container {
  overflow: hidden;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1030px;
}

.col {
  max-width: 320px;
}

.title-h3 {
  color: #333;
  font-size: 13px;
  font-weight: 700;
}

.btn-yellow {
  position: relative;
  background-color: #e9cd6b;
  text-shadow: 0 1px 1px rgba(255, 255, 255, 0.8);
  color: #252525;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 14px;
  padding: 14px 28px;
  border-radius: 30px;
  overflow: hidden;
}

.btn-yellow:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 1.5em;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.6);
  transform: translateX(-4em) skewX(-45deg);
}

.btn-yellow:hover:before {
  animation: move-light .6s;
}

@keyframes move-light {
  from {
    transform: translateX(-4em) skewX(-45deg);
  }
  to {
    transform: translateX(15em) skewX(-45deg);
  }
}

.header {
  padding-top: 34px;
  background-image: url("../../images/header-bg.png");
  background-repeat: repeat;
  color: var(--color-light-gray);
  background-color: #8D451E;
  width: 100%;
  height: 76vh;
  overflow: hidden;
}

.header__container {
  height: 100%;
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 52px;
}

.header__logo {
  display: flex;
  align-items: center;
}

.header__title {
  font-family: "Ronnia", sans-serif;
  font-size: 34px;
  font-weight: 700;
  letter-spacing: -3px;
  margin-right: 20px;
}

.header__text {
  position: relative;
  font-family: Arial, sans-serif;
  font-style: italic;
  font-size: 11px;
}

.header__text::before {
  position: absolute;
  content: "|";
  left: -10px;
}

.header__content {
  text-align: center;
}

.header__subtitle {
  font-family: Georgia, serif;
  font-style: italic;
  font-weight: 300;
  letter-spacing: 1px;
  margin-bottom: 25px;
}

.header__descr {
  font-size: 18px;
  font-weight: 300;
  max-width: 800px;
  margin: 0 auto 54px;
}

.nav__link {
  position: relative;
  display: inline-block;
  font-size: 13px;
  padding: 6px 10px 6px 10px;
  transition: all ease .5s;
}

.nav__link:hover {
  background-color: #8d451e;
  border-radius: 5px;
  transition: all ease .5s;
}

.nav__link--dark {
  background-color: var(--color-dark);
  border-radius: 5px;
}

.nav__link--drop {
  position: relative;
  padding-right: 24px;
  transition: all ease .5s;
}

.nav__link--drop:hover .dropdown-menu {
  display: flex;
  transition: all ease .5s;
}

.nav__link--drop::after {
  position: absolute;
  content: "";
  width: 12px;
  height: 8px;
  top: 50%;
  right: 7px;
  font-size: 13px;
  font-weight: 700;
  color: var(--color-dark);
  background-image: url("../../images/drop-arrow-black.png");
  background-position: center;
  background-repeat: no-repeat;
  transform: translateY(-50%);
}

.nav__link--drop-light::after {
  color: #D7D3CA;
  font-weight: 700;
  background-image: url("../../images/drop-arrow.png");
}

.nav__link + .nav__link {
  margin-left: 15px;
}

.dropdown-menu {
  display: none;
  position: absolute;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #8D451E;
  padding: 5px;
  top: 20px;
  left: 0;
  transition: all ease .5s;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.dropdown-menu li {
  margin: 10px 0;
}

.slider__item.slick-slide {
  position: relative;
  width: 415px;
  height: 400px;
  transform: translateY(40px);
  transition: all ease 1s;
}

.slider__item.slick-slide.slick-current {
  width: 480px;
  transform: translateY(15px);
  transition: all ease 1s;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.9), 0 10px 10px rgba(0, 0, 0, 0.9);
  z-index: 10;
}

.slick-prev {
  position: absolute;
  left: -70px;
  top: 35%;
}

.slick-next {
  position: absolute;
  right: -70px;
  top: 35%;
}

.cta {
  padding: 38px 0 10px;
  background-color: #1d1d1d;
  background-image: url("../../images/cta-bg.png");
  background-repeat: no-repeat;
  background-position: top;
}

.cta__columns {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cta__item {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.cta__item-img {
  position: relative;
  width: 55px;
  height: 55px;
  margin-right: 15px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.8);
  background-color: #d0d0d0;
}

.cta__item-img::before {
  position: absolute;
  content: "";
  width: 28px;
  height: 28px;
  top: -10px;
  left: -10px;
  background-image: url("../../images/plus.png");
  background-position: center;
  background-repeat: no-repeat;
}

.cta__item-title {
  margin-bottom: 11px;
  color: var(--color-light-gray);
}

.cta__item-text {
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  color: var(--color-gray);
  font-size: 12px;
  max-width: 220px;
}

.cta__item-text--long {
  max-width: 290px;
}

.cta__item-btns {
  margin-top: 22px;
}

.cta__btn + .cta__btn {
  margin-left: 10px;
}

.content-section {
  padding: 60px 0 55px;
  background-image: url("../../images/content-bg.png");
  background-repeat: repeat;
  background-color: gray;
  line-height: 20px;
}

.content-section__columns {
  display: flex;
  justify-content: space-between;
}

.content-section__columns p {
  margin-bottom: 10px;
  color: #444444;
}

.content-section__title {
  margin-bottom: 27px;
}

.content-section__img {
  position: relative;
  margin-bottom: 20px;
}

.content-section__img::before {
  position: absolute;
  content: "";
  width: 28px;
  height: 28px;
  top: -10px;
  right: -10px;
  background-image: url("../../images/plus.png");
  background-position: center;
  background-repeat: no-repeat;
}

.content-section__button {
  color: #888888;
  font-family: Georgia, serif;
  font-size: 11px;
  font-style: italic;
  line-height: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: rgba(255, 255, 255, 0.57);
  padding: 5px 28px;
  text-align: center;
  box-shadow: inset 0 0 5px #b3b3b3;
  border-radius: 5px;
}

.callout-section {
  background-image: url("../../images/content-bg.png");
  background-repeat: repeat;
  background-color: gray;
  padding-bottom: 57px;
}

.callout-section__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 27px;
  background: linear-gradient(to bottom, white 0%, #f6f6f6 47%, #ededed 100%);
  border-radius: 3px;
}

.callout-section__title {
  text-shadow: 0 1px 0 #ffffff;
  color: #444444;
  font-family: Georgia, serif;
  font-size: 18px;
  font-style: italic;
  line-height: 20px;
  margin-bottom: 11px;
}

.callout-section__descr {
  color: #444444;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
}

.blog-section {
  padding: 57px 0;
  background-image: url("../../images/content-bg.png");
  background-repeat: repeat;
  background-color: gray;
  border-bottom: 10px solid #e5e2d9;
  border-top: 10px solid #e5e2d9;
}

.blog-section__container {
  display: flex;
  justify-content: space-between;
}

.blog-section__title {
  margin-bottom: 28px;
}

.blog-section__item {
  display: flex;
  margin-bottom: 20px;
  padding-bottom: 20px;
  line-height: 20px;
  border-bottom: 1px solid #e4e1d9;
}

.blog-section__img {
  width: 55px;
  height: 55px;
  background-color: #1d1d1d;
  margin-right: 10px;
}

.blog-section__descr {
  max-width: 216px;
}

.tweet {
  max-width: 290px;
  margin-bottom: 17px;
  padding-bottom: 15px;
  line-height: 20px;
  border-bottom: 1px solid #e4e1d9;
}

.tweet a {
  color: #5597b2;
  text-decoration: underline;
}

.tweet span {
  color: #888888;
  font-style: italic;
  font-size: 11px;
}

.testimonial {
  margin-bottom: 20px;
  line-height: 20px;
}

.testimonial__top {
  display: flex;
  margin-bottom: 20px;
}

.testimonial__img {
  width: 55px;
  height: 55px;
  margin-right: 15px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
  background-color: #111111;
}

.testimonial__author-name {
  color: #444444;
  font-weight: 700;
}

.testimonial__author-link {
  color: #888888;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 11px;
}

.testimonial__text {
  color: #444444;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 12px;
}

.footer__top {
  padding: 40px 0 33px;
  background-color: #1d1d1d;
  background-image: url("../../images/cta-bg.png");
  background-repeat: no-repeat;
  background-position: top;
}

.footer__top .col:first-child {
  max-width: 45%;
}

.footer__container {
  display: flex;
  justify-content: space-between;
}

.footer__title {
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  color: #f1eee5;
  font-size: 13px;
  font-weight: 700;
  margin-bottom: 16px;
}

.footer__text {
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  color: #979797;
  font-size: 12px;
  line-height: 20px;
  margin-bottom: 26px;
}

.footer__contacts {
  display: flex;
  color: #979797;
}

.footer__contact span {
  color: #f1eee5;
}

.footer__contact + .footer__contact {
  margin-left: 15px;
}

.footer__form {
  position: relative;
}

.footer__form input {
  width: 100%;
  padding: 12px 11px 10px;
  color: white;
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.05), inset 0 1px 2px #000000;
  background-color: #101010;
  opacity: 0.99;
  border-radius: 5px;
}

.footer__form input::placeholder {
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  color: #979797;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
}

.footer__form button {
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
  padding: 7px 10px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
  background-image: linear-gradient(to top, #cccccc 0%, #f4f4f4 100%);
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.43);
  color: #4a4a4a;
  font-size: 12px;
  font-weight: 700;
  border-radius: 5px;
}

.footer__bottom {
  background-color: #0d0d0d;
  color: #626262;
  font-size: 12px;
  padding: 20px 0;
}

.footer__bottom span {
  color: #989898;
}
